  background-color: #2F7A8D
}
.color6 {
  background-color: #69CDCB
}
.color7 {
  background-color: #81D37C
}
.color8 {
  background-color: #D0C871
}

footer {
  align-self: center;
  font-size: small;
  color: #fff;
  height: 5vh;
}

