.selection-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 320px;
  height: 80%;
  align-self: center;
}

.selections {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 80%;
  margin: auto;
  flex-wrap: wrap;
  align-self: center;
}

h3 {
  align-self: center;
  color: #fff;
  letter-spacing: 0.1em;
}

p {
  align-self: center;
  text-align: center;
  margin-top: 50px;
  color: #fff;
  letter-spacing: 0.1em;
  font-size: 0.8rem;
}

:root {
  --selection-ball: 50px;
}

.selection-ball {
  width: 50px;
  width: var(--selection-ball);
  height: 50px;
  height: var(--selection-ball);
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  margin: 5px;
  text-align: center;
}

a {
  text-decoration: none;
}
.chosen{
  position: relative;
  top: -35px;
}

.ball {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  align-self: center;  
  margin: 3px;
  width: 35px;
  height: 35px;
  min-height: 35px;
  border-radius: 50%;
}

