.tube {
  display: flex;
  flex-direction: column-reverse;
  width: 45px;
  height: 165px;
  margin: 40px 10px 10px 10px;
  cursor: pointer;
  border-radius: 3px 3px 0.5em 0.5em;
  border-top: none;
  background: #EBEDEF;
  justify-self: center;
  align-self: end;
}

