/* @import url('https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap'); */


.board-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  min-width: 320px;
}

.tubes-container {
  display: grid;
  width: 100%;
  height: 100%;
  align-content: center;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
}

.header {
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: 3rem;
}

.header-controls {
  width: 25%;
  height: 100%;
  margin: 10px;
  border: none;
  background: none;
  color: #EBEDEF;
  font-size: 1rem;
  font-family: 'Maven Pro', sans-serif;

}

.header-controls:hover {
  color: grey;
}

.header-controls:focus {
  outline: none;
  box-shadow: none;
}

button {
  cursor: pointer;
}

